import React from 'react'
import YouTube from 'react-youtube'
import { useRecoilValue } from 'recoil';
import { contentDetailProductState } from '../recoil/atoms';








export default function Youtube() {

    const contentDetailProduct = useRecoilValue(contentDetailProductState);

    return (
        <div>
            <YouTube
                videoId={contentDetailProduct.contentsDto.url} // 
                opts={{
                    width: "130%",
                    height: "230px",
                    playerVars: {
                        autoplay: 0, //   
                        rel: 1,
                        modestbranding: 1, //     
                        loop: 0, // 
                        playlist: contentDetailProduct.contentsDto.url, //    
                    },
                }}
                onReady={(e) => {
                    e.target.mute(); // 
                }}

                onEnd={(e) => { e.target.stopVideo(0); }} //       
            />
        </div>
    );
}
