import React, { useEffect, useState } from 'react'
import logo from '../assets/logo.png'
import location from '../assets/location.png'
import UseCurrentLocation from '../hooks/UseCurrentLocation'
import { Link } from 'react-router-dom'
import ShoppingCarts from './ShoppingCarts'
import { useRecoilValue, useRecoilState } from 'recoil'
import { basketInfoState, isloginState, locationState } from '../recoil/atoms'
import axios from 'axios'
import { accessTokenState, memberIdState } from '../recoil/persist'


export default function Header() {
    const [isLogin, setIsLogin] = useRecoilState(isloginState);
    const locationData = useRecoilValue(locationState);
    const [basketInfo, setBasketInfo] = useRecoilState(basketInfoState);
    const tokenData = useRecoilValue(accessTokenState);
    const memberid = useRecoilState(memberIdState)
    // console.log(" :", memberid[0])
    // console.log(" :", tokenData)
    // console.log(" :", locationData);


    const [addressChecked, setAddressChecked] = useState(false);
    const [showShoppingCart, setShowShoppingCart] = useState(false);
    // const [isLoading, setIsLoading] = useState(false);




    const handleButtonClick = () => {
        setShowShoppingCart(!showShoppingCart); //     .
        console.log(" !")
    }
    // console.log(addressChecked)



    const handleBasketInfo = () => {
        axios
            .get('http://3.37.4.231:8080/basket-info', {
                params: {
                    memberId: memberid[0],
                    latitude: locationData.latitude,
                    longitude: locationData.longitude,
                },
            })
            .then((response) => {
                const basket = response.data.result;
                setBasketInfo(basket);
                // setIsLoading(true);
                console.log(" 1:", basket)
            })
            .catch((error) => {
                console.error('     :', error);
            });
    }

    useEffect(() => {
        const localStorageData = localStorage.getItem('localStorage'); //    
        if (localStorageData) {
            //     
            setIsLogin(true);
        } else {
            //     
            setIsLogin(false);
        }
    }, []);

    const handleLogout = () => {
        localStorage.removeItem('localStorage'); //    
        setIsLogin(false); //   
        window.location.reload(); //  
    };


    useEffect(() => {
        axios
            .post(
                'http://3.37.4.231:8080/api/auth/validate',
                // 'http://192.168.100.31:8080/api/auth/validate',
                { accessToken: tokenData }, //    
            )
            .then((response) => {
                console.log(' ');
                if (response.status === 200) {
                    // window.location.href = '/';
                    console.log('!!!!!');
                } else {
                    localStorage.removeItem('localStorage');
                    window.location.href = '/';
                }
            })
            .catch((error) => {
                if (error.response) {
                    //   
                    console.log("1", error.response.data); //   
                    console.log("2", error.response.status); //   
                    console.log("3", error.response.headers); //  
                } else if (error.request) {
                    //      
                    console.error(' .', error.request);
                } else {
                    //      
                    console.error('', error.message);
                }
                console.error('  :', error);
                // alert("")
            });
    }, []);


    return (
        <header className="bg-white">
            <div className="mx-auto max-w-screen-xl px-4 sm:px-6 lg:px-8">
                <div className="flex h-16 items-center justify-between">
                    <div className="md:flex md:items-center md:gap-4">
                        <Link to="/">
                            <img
                                className="h-10"
                                src={logo}
                                alt='LinCook'
                            >
                            </img>
                        </Link>

                        <Link to="/">
                            <span className='text-3xl font-bold text-green-500'></span>
                        </Link>
                    </div>

                    <div className="hidden md:block">
                        <nav aria-label="Global">
                            <ul className="flex items-center gap-6 text-sm">

                                <li className='ml-4'>
                                    <Link
                                        className="text-black transition hover:text-gray-500/75"
                                        to="/team"
                                    >
                                        Team
                                    </Link>
                                </li>


                                {isLogin ? (
                                    <li className='ml-4'>
                                        <button onClick={handleLogout} className="text-black transition hover:text-gray-500/75"></button>
                                    </li>
                                ) : (
                                    <>
                                        <li className='ml-4'>
                                            <Link
                                                className="text-black transition hover:text-gray-500/75"
                                                to="/login"
                                            >
                                                
                                            </Link>
                                        </li>

                                        <li className='ml-4'>
                                            <Link
                                                className="text-black transition hover:text-gray-500/75"
                                                to="/signup"
                                            >
                                                
                                            </Link>
                                        </li>
                                    </>
                                )}
                                {isLogin && (
                                    <li className='ml-4'>
                                        <p
                                            className="text-black transition hover:text-gray-500/75 cursor-pointer"
                                            onClick={() => {
                                                handleBasketInfo();
                                                handleButtonClick();
                                            }}
                                        >
                                            <div className="relative py-2">
                                                <div className="absolute left-4 top-0"></div>
                                                <svg
                                                    xmlns="http://www.w3.org/2000/svg"
                                                    fill="none"
                                                    viewBox="0 0 24 24"
                                                    strokeWidth="1.5"
                                                    stroke="currentColor"
                                                    className="w-6 h-6"
                                                >
                                                    <path
                                                        strokeLinecap="round"
                                                        strokeLinejoin="round"
                                                        d="M2.25 3h1.386c.51 0 .955.343 1.087.835l.383 1.437M7.5 14.25a3 3 0 00-3 3h15.75m-12.75-3h11.218c1.121-2.3 2.1-4.684 2.924-7.138a60.114 60.114 0 00-16.536-1.84M7.5 14.25L5.106 5.272M6 20.25a.75.75 0 11-1.5 0 .75.75 0 011.5 0zm12.75 0a.75.75 0 11-1.5 0 .75.75 0 011.5 0z"
                                                    />
                                                </svg>
                                            </div>
                                        </p>
                                    </li>
                                )}
                                {showShoppingCart && (
                                    <ShoppingCarts open={showShoppingCart} onClose={() => setShowShoppingCart(false)} />
                                )}
                            </ul>
                        </nav>
                    </div>

                    <div className="flex items-center gap-6 text-sm mt-0">
                        <div className="sm:flex sm:gap-4 mr-20">
                            <img
                                className="h-5"
                                src={location}
                                alt='location'
                                onClick={() => window.location.reload()}
                            >
                            </img>
                            <span>
                                <UseCurrentLocation
                                    addressChecked={addressChecked}
                                    setAddressChecked={setAddressChecked}
                                />
                            </span>

                            <div className="sm:flex sm:gap-4 ml-6">
                                <Link
                                    className="text-black transition"
                                    to="/signup"
                                >
                                </Link>
                            </div>

                        </div>
                    </div>

                </div>
            </div>
        </header >
    )
}